import React, { useState, useEffect } from 'react'
import { useHistory, useLocation } from 'react-router-dom'
import {
  CBadge,
  CCard,
  CCardBody,
  CCardHeader,
  CDropdown,
  CDropdownItem,
  CDropdownMenu,
  CDropdownToggle,
  CCol,
  CDataTable,
  CRow,
  CPagination
} from '@coreui/react'

import getUsers from './UsersData'

const getBadge = status => {
  switch (status) {
    case true: return 'success'
    default: return 'danger'
  }
}

const Users = () => {
  const history = useHistory()
  const queryPage = useLocation().search.match(/page=([1-9]+)/, '')
  const currentPage = Number(queryPage && queryPage[1] ? queryPage[1] : 1)
  const [page, setPage] = useState(currentPage)
  const [state, setState] = useState({users: [], itemsPerPage: 10})

  const pageChange = newPage => {
    currentPage !== newPage && history.push(`/users?page=${newPage}`)
  }

  useEffect(() => {
    currentPage !== page && setPage(currentPage)
  }, [currentPage, page])

  useEffect(() => {
    console.log("Buscando users");
    getUsers().then( ({data}) => {
      console.log(data);
      setState(state => ({...state, users: data}))
      console.log("Buscou de vdd.")
    })
    console.log("Buscou.");
  }, [])

  return (
    <CRow>
      <CCol xl={6}>
        <CCard>
          <CCardHeader>
            <CRow className="py-3">
            
            <CCol className="py-3">
            Acampantes cadastrados
            </CCol>
            <CCol>
           
            <CDropdown className="mt-2">
              <CDropdownToggle caret color="info">
                Mostrar {state.itemsPerPage} acampantes
              </CDropdownToggle>
              <CDropdownMenu>
                {[1,2,5,10, 20, 30].map( (qtd, index) => <CDropdownItem onClick={() => setState(state => ({...state, itemsPerPage: qtd})) } key={index.toString()}>{qtd}</CDropdownItem>)}
              </CDropdownMenu>
            </CDropdown>
            </CCol>
            </CRow>
          </CCardHeader>
          <CCardBody>
          <CDataTable
            items={state.users}
            fields={[
              { key: 'name', _classes: 'font-weight-bold' },
              'cpf', 'email', {key: 'isConfirmado', label: 'Confirmado'}
            ]}
            hover
            noItemsViewSlot="A"
            striped
            itemsPerPage={state.itemsPerPage}
            activePage={page}
            clickableRows
            onRowClick={(item) => history.push(`/users/${item._id}`)}
            scopedSlots = {{
              'isConfirmado':
                (item)=>(
                  <td>
                    <CBadge color={getBadge(item.isConfirmado)}>
                      {item.isConfirmado ? 'Sim' : 'NÃ£o'}
                    </CBadge>
                  </td>
                )
            }}
          />
          <CPagination
            activePage={page}
            onActivePageChange={pageChange}
            pages={Math.ceil(state.users.length / state.itemsPerPage)}
            doubleArrows={false} 
            align="center"
          />
          </CCardBody>
        </CCard>
      </CCol>
    </CRow>
  )
}

export default Users
